<template>
  <el-dialog
    :visible.sync="dialogVisible"
    v-bind="normalizedDialogProps"
    v-on="normalizedDialogEvents"
  >
    <!--  dialog title -->
    <!-- start -->
    <template v-if="renderTitle" #title>
      <CustomRender :render="() => renderTitle(form)" />
    </template>
    <!-- end -->
    <el-form
      ref="dialogFormRef"
      :model="form"
      class="pro-form"
      :class="className"
      v-bind="formProps"  
    >
      <!--  -->
      <!-- start -->
      <template v-if="grid">
        <el-row v-bind="rowProps">
          <el-col 
            v-for="formItem in normalizedFormItems" 
            v-bind="formItem.colProps"
            :key="formItem.prop ?? getUID()"
          >
            <!--  el-form-item -->
            <!-- start -->
            <slot 
              v-if="formItem.customSlot"
              :name="formItem.customSlot === true ? formItem.prop : formItem.customSlot"
              v-bind="{ form }"
            ></slot>
            <CustomRender
              v-else-if="formItem.renderFormItem"
              :render="() => formItem.renderFormItem(form)"
            />
            <!-- end -->
            <!-- ProFormItem -->
            <!-- start -->
            <ProFormItem
              v-else
              :form="form" 
              :formItem="formItem"
            >
              <template #[formItem.prop]>
                <slot :name="formItem.prop" v-bind="{ form, formItem }"></slot>
              </template>
            </ProFormItem>
            <!-- end -->
          </el-col>
        </el-row>
      </template>
      <!-- end -->
      <!--  -->
      <!-- start -->
      <template v-else>
        <template v-for="formItem in normalizedFormItems">
          <!--  el-form-item -->
          <!-- start -->
          <slot 
            v-if="formItem.customSlot"
            :name="formItem.customSlot === true ? formItem.prop : formItem.customSlot"
            v-bind="{ form }"
          ></slot>
          <CustomRender
            v-else-if="formItem.renderFormItem"
            :render="() => formItem.renderFormItem(form)"
          />
          <!-- end -->
          <!-- ProFormItem -->
          <!-- start -->
          <ProFormItem
            v-else
            :form="form" 
            :formItem="formItem"
            :key="formItem.prop ?? getUID()"
          >
            <template #[formItem.prop]>
              <slot :name="formItem.prop" v-bind="{ form, formItem }"></slot>
            </template>
          </ProFormItem>
          <!-- end -->
        </template>
      </template>
    </el-form>
    <!-- end -->
    <!-- submitter -->
    <!-- start -->
    <template v-if="submitter" #footer>
      <!--  submitter -->
      <!-- start -->
      <template v-if="submitter.customRender">
        <Submitter
          :submitter="submitterProps" 
          :action="{ submit, close, reset, resetAllFields }"
          :render="(action, doms) => submitter.customRender(form, action, doms)" 
        />
      </template>
      <!-- end -->
      <!--  submitter -->
      <!-- start -->
      <template v-else>
        <span class="dialog-form-footer">
          <el-button v-bind="submitterProps.cancelButtonProps" @click="close" >{{ submitterProps.cancelText }}</el-button>
          <el-button type="primary" v-bind="submitterProps.submitButtonProps" @click="submit">{{ submitterProps.submitText }}</el-button>
        </span>
      </template>
      <!-- end -->
    </template>
    <!-- end -->
  </el-dialog>
</template>

<script>
  import CustomRender from '@packages/custom-render'
  import ProFormItem from '@packages/pro-form-item'
  import Submitter from './components/Submitter'
  import { generateCryptoUID } from '@/utils/uid'
  import { setPlaceholder, setSelectOptions, setCascaderOptions } from '@/utils/form'

  export default {
    name: 'DialogForm',
    model: {
      prop: 'visible',
      event: 'close'
    },
    components: {
      CustomRender,
      ProFormItem,
      Submitter
    },
    props: {
      // 
      visible: {
        type: Boolean,
        default: false
      },
      // 
      title: {
        type: String
      },
      // 
      renderTitle: {
        type: Function,
      },
      // 
      width: {
        type: String
      },
      // el-dialog attributes 
      dialogProps: {
        type: Object,
        default: () => ({})
      },
      // el-dialog events 
      dialogEvents: {
        type: Object,
        default: () => ({})
      },
      // el-form attributes 
      formProps: {
        type: Object,
        default: () => ({})
      },
      // el-form 
      className: {
        type: String
      },
      // 
      formItems: {
        type: Array,
        required: true,
        default: () => []
      },
      // 
      submitter: {
        type: [Boolean, Object],
        default: true
      },
      // 
      grid: {
        type: Boolean,
      },
      //  grid  el-row
      rowProps: {
        type: Object,
        default: () => ({ gutter: 8 })
      },
      // 
      initialValues: {
        type: Object,
        default: () => ({})
      },
    },
    computed: {
      //  dialog props
      normalizedDialogProps() {
        const { title, width, dialogProps = {} } = this
        const customClass = dialogProps['custom-class']
        return {
          ...dialogProps,
          'custom-class': customClass ? `${customClass} dialog-form` : 'dialog-form',
          visible: undefined, // 
          title,
          width,
        }
      },
      //  dialog events
      normalizedDialogEvents() {
        const { dialogEvents, close } = this
        
        const originalClose = dialogEvents.close
        //  close  close 
        if (originalClose) {
          dialogEvents.close = () => {
            originalClose()
            close()
          }

          return {
            ...dialogEvents
          }
        } 
        
        return { close }
      },
      // formItems 
      normalizedFormItems() {
        return this.formItems
          // 
          .filter(item => !item.hideInForm)
          .map(item => {
            const { valueType, fieldProps = {} } = item
            //  placeholder
            setPlaceholder(fieldProps, valueType)

            //  select options
            setSelectOptions(item, this.cachedOptions)

            //  cascader options
            setCascaderOptions(fieldProps, item, this.cachedOptions)

            return {
              ...item,
              fieldProps
            }
          })
      },
      // submitter 
      submitterProps() {
        if (this.submitter) {
          // 
          const defaultTextConfig = {
            cancelText: '', 
            submitText: ''
          }

          // 
          if (typeof this.submitter === 'object') {
            return {
              ...defaultTextConfig,
              ...this.submitter
            }
          }

          // 
          return {
            ...defaultTextConfig
          }
        }

        return false
      }
    },
    data() {
      return {
        dialogVisible: false, // 
        cachedOptions: {}, //  { [prop]: res }
        form: this.initForm(), // 
      }
    },
    watch: {
      //  visible
      visible: {
        handler(newValue) {
          this.dialogVisible = newValue
        },
        immediate: true
      }
    },
    created () {
      // 
      this.getOptions()
    },
    methods: {
      /**
       * @desc 
       */
      getOptions() {
        for (const item of this.formItems) {
          const { prop, optionLoader } = item
          if (typeof optionLoader === 'function') {
            optionLoader().then(res => {
              this.cachedOptions = {
                ...this.cachedOptions,
                [prop]: res
              }
            })
          }
        }
      },
      /**
       * @desc 
       */
      initForm() {
        const { formItems, initialValues } = this
        const data = formItems.reduce((accu, cur) => {
          const { prop, initialValue } = cur
          if (!prop) {
            return accu
          }

          return {
            ...accu,
            [prop]: initialValue
          }
        }, {})

        return { ...initialValues, ...data }
      },
      /**
       * @desc  el-form ref
       * @returns {Object}
       */
      getFormRef() {
        return this.$refs.dialogFormRef
      },
      /**
       * @desc 
       * @returns {Object}
       */
      getForm() {
        return this.form
      },
      /**
       * @desc 
       * @param {Object} data 
       */
      setFieldsValue(data) {
        this.form = {
          ...this.form,
          ...data
        }
      },
      /**
       * @desc 
       * @param {String} key 
       * @param {any} value 
       */
      setFieldValue(key, value) {
        this.form[key] = value
      },
      /**
       * @desc Dialog 
       */
      close() {
        this.$emit('close', false)
      },
      /**
       * @desc 
       */
      submit() {
        this.$refs.dialogFormRef?.validate((valid, object) => {
          if (valid) {
            // 
            this.$emit('onFinish', this.form)
            return
          }

          // 
          this.$emit('onError', object)
        })
      },
      /**
       * @desc 
       */
      async reset() {
        try {
          await this.$refs.dialogFormRef?.resetFields()
          // 
          this.$emit('onReset')
        } catch (err) {
          console.error('err', err)
        }
      },
      /**
       * @desc 
       */
      resetAllFields() {
        // 
        this.$refs.dialogFormRef?.clearValidate()
        // 
        this.form = this.initForm()
      },      
      /**
       * @desc  uid
       */
      getUID() {
        return generateCryptoUID()
      }
    }
  }
</script>

