// 
import ProForm from '../packages/pro-form'
import DialogForm from '../packages/dialog-form'
import ProTable from '../packages/pro-table'
import EditableProTable from '../packages/editable-pro-table'

// 
const components = [
  ProForm,
  DialogForm,
  ProTable,
  EditableProTable
]

//  1.  install  -  Vue.use(ElementUIProComponents)
const install = function(Vue) {
  // 
  if (install.installed) {
    return
  }
  
  // 
  components.forEach(component => {
    Vue.component(component.name, component)
  })
  
  // 
  install.installed = true
}

//  2.  script 
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

//  3.  install 
export default {
  install,
  ProForm,
  DialogForm,
  ProTable,
  EditableProTable
}

//  4. 
export {
  ProForm,
  DialogForm,
  ProTable,
  EditableProTable
}
