<template>
  <el-form-item
    v-bind="normalizedFormItem"
  >
    <template v-if="formItem.renderLabel" #label>
      <!--  label -->
      <!-- start -->
      <CustomRender :render="formItem.renderLabel" />
      <!-- end -->
    </template>
    <!--  -->
    <!-- start -->
    <CustomRender 
      v-if="formItem.renderField"
      :render="() => formItem.renderField({ form, formItem })" 
    />
    <slot 
      v-else-if="formItem.valueType === 'slot'" 
      :name="formItem.prop"
    ></slot>
    <!-- end -->
    <!--  -->
    <!-- start -->
    <component
      v-else
      :is="`el-${formItem.valueType}`"
      v-model="form[formItem.prop]"
      v-bind="formItem.fieldProps"
      v-on="formItem.fieldEvents"
    >
      <!-- Select -->
      <!-- start -->
      <template v-if="formItem.valueType === 'select'">
        <!-- Option Group -->
        <!-- start -->
        <template v-if="formItem.options?.[0]?.options">
          <el-option-group
            v-for="group in formItem.options"
            v-bind="group"
            :key="group.label"
          >
            <el-option 
              v-for="option in group.options"
              v-bind="option"
              :key="option.value"
            ></el-option>
          </el-option-group>
        </template>
        <!-- end -->
        <!-- Option -->
        <!-- start -->
        <template v-else>
          <el-option 
            v-for="option in formItem.options"
            v-bind="option"
            :key="option.value"
          ></el-option>
        </template>
        <!-- end -->
      </template>
      <!-- end -->
      <template v-else-if="groups.includes(formItem.valueType)">
        <component
          :is="selection[formItem.valueType]"
          v-for="option in formItem.options"
          :label="option.value"
          :key="option.value"
        >
          {{ option.label }}
        </component>
      </template>
    </component>
    <!-- end -->
  </el-form-item>
</template>

<script>
  import CustomRender from '../custom-render'

  export default {
    name: 'ProFormItem',
    components: {
      CustomRender,
    },
    props: {
      // formItem 
      formItem: {
        type: Object,
        required: true
      },
      // 
      form: {
        type: Object,
        required: true
      },
    },
    computed: {
      //  el-form-item 
      normalizedFormItem() {
        const {
          renderLabel,
          valueType,
          renderField,
          fieldProps, 
          fieldEvents, 
          options,
          valueEnum, 
          optionLoader, 
          initialValue, 
          colProps,
          ...keys
        } = this.formItem
        return keys 
      },
    },
    data() {
      return {
        groups: ['radio-group', 'checkbox-group'], // valueType
        selection: { // 
          'radio-group': 'el-radio',
          'checkbox-group': 'el-checkbox'
        }
      }
    },
  }
</script>

<style scoped>

